import React, { useEffect, useState } from 'react';
import io, { Socket } from 'socket.io-client';

function App() {

    const [id, setId] = useState('');
    // let client = io('https://rtc-test-11.herokuapp.com/');

    let client = io('http://localhost:8000');
    let localVideo = document.getElementById('localVideo'),
        remoteVideo = document.getElementById('remoteVideo');

    // const userMedia = navigator.getUserMedia || navigator.webkitGetUserMedia || navigator.mozGetUserMedia;


    const iceServers = [
        { urls: 'stun:stun.l.google.com:19302' },
        { urls: 'stun:stun1.l.google.com:19302' },
        { urls: 'stun:stun2.l.google.com:19302' },
        { urls: 'stun:stun3.l.google.com:19302' },
        { urls: 'stun:stun4.l.google.com:19302' },
    ];

    let rtc;// = new RTCPeerConnection({ iceServers: iceServers });



    const connect = () => {
        client.emit('join', id);
    }



    const call = () => {
        rtc.createOffer().then(offer => {
            rtc.setLocalDescription(new RTCSessionDescription(offer));
            client.emit('offer', { id: id, offer: offer });
        })
        rtc.onicecandidate = e => { if (e.candidate) client.emit('candidate', { id: id, candidate: e.candidate }) }
    }


    const answer = () => {
        rtc.createAnswer().then(answer => {
            rtc.setLocalDescription(new RTCSessionDescription(answer));
            client.emit('answer', { id: id, answer: answer });
        })
        rtc.onicecandidate = e => { if (e.candidate) client.emit('candidate', { id: id, candidate: e.candidate }) }
    }


    const end = () => {
        client.emit('end', { id: id });
    }

    client.on('end', event => {
        endCall();
    })

    client.on('full_room', event => {
        console.log('Room full: ', event);
        alert(`Room ${id} is full.`)
    })

    client.on('join', event => {
        console.log('Join: ', event);
        rtc = new RTCPeerConnection({ iceServers: iceServers });
        setVideo(rtc);
    })

    client.on('offer', event => {
        console.log('Offer');
        rtc.setRemoteDescription(new RTCSessionDescription(event));
    });


    client.on('answer', event => {
        console.log('Answer');
        rtc.setRemoteDescription(new RTCSessionDescription(event));
    })


    client.on('candidate', event => {
        rtc.addIceCandidate(new RTCIceCandidate(event.candidate));
    })


    client.on('end', event => {
        endCall();
    })

    const endCall = () => {
        if (rtc) {
            rtc.ontrack = null;
            rtc.onicecandidate = null;
        }

        if (remoteVideo.srcObject) remoteVideo.srcObject.getTracks().forEach(track => track.stop());


        if (localVideo.srcObject) localVideo.srcObject.getTracks().forEach(track => track.stop());


        rtc.close();
        rtc = new RTCPeerConnection({ iceServers: iceServers });
    }

    const setVideo = (rtc) => {
        navigator.mediaDevices.getUserMedia({ video: true, audio: false }).then(stream => {
            localVideo.srcObject = stream;
            stream.getTracks().forEach(track => rtc.addTrack(track, stream));
        });

        rtc.ontrack = e => {
            remoteVideo.srcObject = e.streams[0];
        }
    }

    return (
        <>
            <input type='text' value={id} onChange={e => setId(e.target.value)} />

            <button onClick={e => connect()}>Connect</button>
            <button onClick={e => call()}>Call</button>
            <button onClick={e => answer()}>Answer</button>
            <button onClick={e => end()}>End</button><br />
            <video id='localVideo' autoPlay height={200}></video>
            <video id='remoteVideo' autoPlay height={200}></video>
        </>
    )
}

export default App;